.Project {
  width: 310px;
  height: 190px;
  position: relative;
  text-align: center;
  overflow: hidden;
  padding: 0px;
  border-radius: 10px;
  margin: 20px;
  align-self: flex-start;
}

.Project img {
  width: 100%;
  height: 100%;
}

.Project p {
  text-align: center;
  width: 100%;
  position: absolute;
  bottom: 0px;
  display: block;
  padding: 10px;
  background-color: rgba(10, 10, 10, 0.808);
  color: rgb(245, 245, 245);
  margin: 0px 0px;
}

.Project:hover {
  filter: brightness(0.5);
  cursor: pointer;
}

@media only screen and (max-width: 700px) {
  .Project {
    width: 300px;
    height: 180px;
    margin: 5px;
  }
}
