.Home {
  padding: 70px;
}

.Home .main-block {
  width: 100%;
  background-color: rgb(245, 245, 245);
  height: 400px;
  /* border-top-right-radius: 100px; */
}

.Home .upper-block {
  height: 60%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 10%;
  overflow: hidden;
}
.Home .upper-block img {
  /* margin-top: 110px; */
  height: 250px;
  width: 250px;
  /* transform: rotate(-10deg); */
  /* border: solid 3px white; */
  /* border-top-right-radius: 100px; */
  border-radius: 50%;
  /* filter: brightness(0.9); */
}
.Home .upper-block img:hover {
  filter: brightness(0.5);
}

.Home .upper-block .home-info {
  text-align: left;
}

.Home .upper-block .home-info h1 {
  font-family: "Neucha", cursive;
  font-size: 3rem;
  text-transform: uppercase;
  font-weight: 500;
  margin: -20px 0px;
  /* width: 300px; */
}

.Home .upper-block .home-info p {
  font-family: "Neucha", cursive;
  font-size: 1.2rem;
  font-weight: 600;
}

.Home .lower-block {
  height: 40%;
  display: flex;

  background-color: rgb(255, 196, 0);
}

.Home .left-block {
  width: 20px;
  height: 100%;
  background-color: rgb(29, 29, 29);
}
.Home .right-block {
  width: 100%;
  display: flex;
  justify-content: space-between;
  /* align-items: center; */
}

.Home .right-block div {
  padding-right: 10px;
  text-align: left;
}

.Home .right-block p {
  font-family: Georgia, "Times New Roman", Times, serif;
  color: rgb(245, 245, 245);
  font-weight: 600;
  font-size: 1.2rem;
  /* text-align: right; */
}

.Home .right-block i {
  margin-left: 30px;
  font-size: 1.4rem;
  text-align: right;
  color: rgb(245, 245, 245);
}

.Home .right-block .recent-frameworks {
  color: rgba(245, 245, 245, 1);
  margin-left: 10px;
  font-family: Georgia, "Times New Roman", Times, serif;
  font-size: 2rem;

  height: 120px;
  margin: auto 10px;
}
.Home .right-block .recent-frameworks h1 {
  color: rgba(245, 245, 245, 1);
  margin-top: -5px;
  margin-bottom: -5px;
  background-color: rgba(29, 29, 29, 1);
  padding: 10px;

  align-items: center;
  font-size: 2rem;
}
.Home .right-block .recent-frameworks p {
  color: rgba(245, 245, 245, 1);
  font-size: 0.8rem;
}

.Home .right-block .recent-frameworks .down-right {
  text-align: right;
}

.Home .technology {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: -10px;
}
@media only screen and (max-width: 900px) {
  .Home {
    padding: 50px;
  }
}
@media only screen and (max-width: 700px) {
  .Home {
    padding: 20px;
    padding-bottom: 60px;
  }
  .Home .main-block {
    width: 100%;
    background-color: rgb(245, 245, 245);
    height: 700px;
    /* border-top-right-radius: 100px; */
  }

  .Home .upper-block {
    height: 50%;
    display: block;
    text-align: center;
  }
  .Home .lower-block {
    height: 50%;
  }

  .Home .right-block {
    width: 100%;
    display: block;
    /* align-items: center; */
  }

  .Home .technology {
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin-bottom: -10px;
  }

  .Home .right-block .recent-frameworks h1 {
    font-size: 1.1rem;
    text-align: center;
  }
  .Home .upper-block .home-info h1 {
    text-align: center;
    font-size: 1.6rem;
    margin: 0px 0px;
    /* width: 300px; */
  }

  .Home .upper-block .home-info p {
    text-align: center;
    font-size: 0.8rem;
  }

  .Home .upper-block img {
    /* margin-top: 110px; */
    height: 200px;
    width: 200px;
    /* transform: rotate(-10deg); */
    /* border: solid 3px white; */
    /* border-top-right-radius: 100px; */
    border-radius: 50%;
    /* filter: brightness(0.9); */
  }
}

.centered-div {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.welcome-message {
  font-size: 5rem;
  font-family: "Neucha", cursive;
  font-weight: 600;
  color: rgb(29, 29, 29);
}

.welcome-message i {
  color: rgb(255, 196, 0);
}
