.Contact {
  padding: 70px;
  font-family: "Neucha", cursive;
}

.Contact form {
  margin: auto auto;
  background-color: rgb(245, 245, 245);
  padding: 20px;
  display: flex;
  flex-direction: column;
  border-radius: 20px;
}
.Contact input {
  display: block;
  width: 80%;
  height: 30px;
  margin: 10px auto;
  padding: 0px 10px;
  border-radius: 20px;
  border: solid 2px black;
}
.Contact input:focus {
  outline: none;
  border: solid 2px rgb(255, 196, 0);
}

.Contact textarea {
  display: block;
  width: 80%;
  height: 30px;
  margin: 10px auto;
  padding: 10px;
  height: 100px;
  resize: none;
  border-radius: 20px;
  border: solid 2px black;
}
.Contact textarea:focus {
  outline: none;
  border: solid 2px rgb(255, 196, 0);
}

.Contact label {
  font-weight: 800;
  align-self: auto;
}
.Contact .fa-at {
  margin-right: 10px;
}

#email-submit {
  width: 100px;

  background-color: rgb(255, 196, 0);
  border: none;
  color: white;
  /* font-family: Georgia, "Times New Roman", Times, serif; */
  font-size: 1.2rem;
  font-weight: 700;
  text-transform: uppercase;
}
#email-submit:hover {
  cursor: pointer;

  font-size: 1.1rem;
}

.Contact .fa-envelope {
  margin-right: 10px;
}

.Contact #disabled {
  background-color: gray;
  color: white;
  border: none;
  font-size: 1.2rem;
  font-weight: 700;
  text-transform: uppercase;
  width: 100px;
}
.Contact .far {
  margin-right: 10px;
}
